import React, {useEffect} from 'react';
import { Route, Switch } from 'react-router-dom';
import './App.css';
// import firebase from './firebase';
import Navbartop from './Navbartop';

import LoginForm from './LoginForm';
import Home from './Home';
import ErrorPage from './ErrorPage';
import DonateMoney from './donate-money';
import DonateFood from './donate-food';
import Footerbottom from './Footerbottom';
import MyCart from './my-cart';
import Admin from './Admin';
import Confirmation from './confirmation';


const App = () => {
  async function begin() {
    console.log("starting up");

    let userType;
    if (/Android|webOS|iPhone|iPad|iPod|BlackBerry|IEMobile|Opera Mini/i.test(navigator.userAgent)) {
      userType = "Mobile";
      document.getElementsByClassName("App")[0].className = userType;
    } else {
      userType = "Computer";
    }
    console.log(userType);

    let savedEmail = sessionStorage.getItem("savedEmail");
    if (savedEmail) {
      console.log("savedmail is " + savedEmail);
      document.getElementsByClassName("Navbartop")[0].style.display = "block";
      document.getElementsByClassName("Footerbottom")[0].style.display = "block";
    } else {
      document.getElementsByClassName("Navbartop")[0].style.display = "none";
      document.getElementsByClassName("Footerbottom")[0].style.display = "none";
    }
  }

  useEffect(() => {        
    begin();    
  });

  return (
    <div className="App">
      <Navbartop />

      <Switch>
        <Route path="/" component={LoginForm} exact />
        <Route path="/home" component={Home} />
        <Route path="/donate-money" component={DonateMoney} />
        <Route path="/donate-food" component={DonateFood} />
        <Route path="/my-cart" component={MyCart} />
        <Route path="/admin" component={Admin} />
        <Route path="/confirmation" component={Confirmation} />
        
        <Route component={ErrorPage} />
      </Switch>
      <Footerbottom />
    </div>
  );
}

export default App;
