import React, {useState, useEffect} from 'react';
import { useHistory } from "react-router-dom";
import Form from 'react-bootstrap/Form';
import Button from 'react-bootstrap/Button';
import Accordion from 'react-bootstrap/Accordion';
import Card from 'react-bootstrap/Card';
import Spinner from 'react-bootstrap/Spinner';
import firebase from './firebase';

const Confirmation = () => {
    const [pageLoaded, setPageloaded] = useState(0);
    const [cardNumber, setCardnumber] = useState("");
    const [name, setName] = useState("");
    const history = useHistory();
    
    useEffect(() => {
        if (pageLoaded === 0) {
            let savedEmail = sessionStorage.getItem("savedEmail");
            if (savedEmail) {
                console.log("savedmail is " + savedEmail);
                confirmPayment();
            } else {
                history.push("/");
            }
        }        
    });

    async function confirmPayment() {
        // sessionStorage.setItem("myCart", null);
        setPageloaded(1);

        // get receipt confirmation
        let html = "";
        let myList = [];
        let myCart = sessionStorage.getItem("myCart");
        if (myCart) {    
            let myItems = myCart.split(",");            
            if (!(myItems.length === 1 && myItems[0] === "null")) {
                html = `<table class="table"><tbody><tr><th scope="col">Item</th><th scope="col">Qty</th><th scope="col">Total</th></tr>`;
                console.log(myItems);
                let food = await firebase.getFood();
                let total = 0;
                for (let i = 0; i < myItems.length; i++) {
                    for (let j = 0; j < food.length; j++) {
                        if (myItems[i] === food[j].desc) {
                            let newItem = true;
                            for (let k = 0; k < myList.length; k++) {
                                if (myList[k].desc === myItems[i]) {
                                    let item = {
                                        desc: myList[k].desc,
                                        qty: myList[k].qty + 1,
                                        total: parseFloat(myList[k].total) + parseFloat(food[j].price)
                                    }
                                    myList[k] = item;
                                    k = myList.length;
                                    newItem = false;
                                }
                            }
                            if (newItem === true) {
                                let item = {
                                    desc: myItems[i],
                                    qty: 1,
                                    total: parseFloat(food[j].price)
                                }
                                myList.push(item);
                            }
                            j = food.length;
                        }
                    }
                }

                for (let i = 0; i < myList.length; i++) {
                    html += `<tr><td>` + myList[i].desc + `</td><td>` + myList[i].qty + `</td><td>$` + myList[i].total + `</td></tr>`;
                    total += parseFloat(myList[i].total);
                }
                html += `<tr><td><b>Total</b></td><td></td><td><b>$` + total + `</b></td></tr>`;
                html += `</tbody></table>`;
                
                // get receipt number 
                let url = window.location.href;
                if (url.includes("receipt=") && url.includes("#")) {
                    let receiptNum = url.split("receipt=")[1].split("#");
                    document.getElementById("receiptID").innerHTML = "Your receipt number is <b>" + receiptNum + "</b>. Please include this in your reference number during payment.";
                }

                // get delivery details
                let orderDetails = sessionStorage.getItem("orderDetails");
                let obj = JSON.parse(orderDetails);
                document.getElementById("deliveryDetails").innerHTML = `
                <h3>Delivery Details</h3>
                <p>You can edit this <a href='#my-cart'>here</a></p>
                <table><tbody>
                <tr><th>Name:</th><td>` + obj.name + `</td></tr>
                <tr><th>Address:</th><td>` + obj.address + `</td></tr>
                </tbody></table>
                `;
            } else {
                html = "<p>You haven't selected any items to be donated. Please <a href='#donate-food'>Click here</a> to select items to donate</p>";
            }        
        } else {
            html = "<p>You haven't selected any items to be donated. Please <a href='#donate-food'>Click here</a> to select items to donate</p>";
        } 
        document.getElementsByClassName("spinner")[0].style.display = "none";
        document.getElementById("myItems").innerHTML = html;
    }

    function makeCardDonation() {
        console.log("making card donaiton");
    }

    return (
        <div className="Confirmation">
            <div className="content">
                <br />
                <div id="alertNotification"></div>                
                <br />
                <h3>Confirm donation</h3>
                <br />
                <p>Thank you for your generosity. Please enter the payment details below:</p>
                <br />
                <h4>Your receipt:</h4>
                <div id="receiptID"></div>
                <br />                
                <a href="my-cart">Alter order</a>
                <br />                
                <br />                
                <br />
                <Spinner animation="border" className="spinner" />
                <div id="myItems"></div>
                <br />                
                <br />
                <hr></hr>
                <div id="deliveryDetails"></div>
                <br />                
                <hr></hr>
                <div className="paymentSection">
                    <Accordion defaultActiveKey="0">
                        <Card>
                            <Card.Header>
                            <Accordion.Toggle as={Button} variant="link" eventKey="0">
                                Bank transfer
                            </Accordion.Toggle>
                            </Card.Header>
                            <Accordion.Collapse eventKey="0">
                            <Card.Body>
                                <table>
                                    <tbody>
                                        <tr>
                                            <td>BSB:</td>
                                            <td>0123 4567</td>
                                        </tr>
                                        <tr>
                                            <td>Account Number:</td>
                                            <td>0123 4567 8901</td>
                                        </tr>
                                        <tr>
                                            <td>Reference:</td>
                                            <td id="referenceID">0123 4567 8901</td>
                                        </tr>
                                    </tbody>
                                </table>
                            </Card.Body>
                            </Accordion.Collapse>
                        </Card>
                        <Card>
                            <Card.Header>
                            <Accordion.Toggle as={Button} variant="link" eventKey="1">
                                Credit Card Payment
                            </Accordion.Toggle>
                            </Card.Header>
                            <Accordion.Collapse eventKey="1">
                            <Card.Body>
                                <Form onSubmit={makeCardDonation}>
                                    <Form.Group controlId="formBasicCardNumber">
                                        <Form.Label>Card Number</Form.Label>
                                        <Form.Control required value={cardNumber} onChange={(e) => setCardnumber(e.target.value)} type="text" placeholder="Enter Card Number" />
                                    </Form.Group>
                                    <Form.Group controlId="formBasicName">
                                        <Form.Label>Name on card</Form.Label>
                                        <Form.Control required value={name} onChange={(e) => setName(e.target.value)} type="text" placeholder="Name on card" />
                                    </Form.Group>
                                    <Form.Group controlId="formBasicCheckbox">
                                        <Form.Check type="checkbox" label="Remember my login details" />
                                    </Form.Group>
                                    <Button variant="primary" type="submit">
                                        Submit
                                    </Button>
                                </Form>
                            </Card.Body>
                            </Accordion.Collapse>
                        </Card>
                    </Accordion>
                </div>      

            </div>
            <br />            
            <br />            

        </div>      
    );
}

export default Confirmation;
