import React, {useState, useEffect} from 'react';
import { useHistory } from "react-router-dom";
import firebase from './firebase';
import Form from 'react-bootstrap/Form';
import Button from 'react-bootstrap/Button';
import Alert from 'react-bootstrap/Alert';
import Spinner from 'react-bootstrap/Spinner';

const LoginForm = () => {
    const [pageLoaded, setPageloaded] = useState(0);
    const [email, setEmail] = useState("");
    const [password, setPassword] = useState("");
    const [password2, setPassword2] = useState("");
    const [firstName, setFirstname] = useState("");
    const [lastName, setLastname] = useState("");
    const [phoneNumber, setPhonenumber] = useState("");
    const [address, setAddress] = useState("");
    const [token, setToken] = useState("");

    const history = useHistory();

    async function begin() {
        setPageloaded(1);
        document.getElementsByClassName("spinner")[0].style.display = "block";
        let url = window.location.href;
        console.log("url:" + url);
        let tkn = "";
        let validated = false;
        if (url.includes("token=") && url.includes("#")) {
            tkn = url.split("token=")[1].split("#")[0];
            setToken(tkn);
            let tokens = await firebase.getTokens();
            if (tokens) {
                for (let i = 0; i < tokens.length; i++) {
                    if (tokens[i].code === tkn) {
                        if (tokens[i].is_valid === 1) {
                            let dt = new Date();
                            let d = dt.getUTCDate();
                            if (d < 10) {
                                d = "0" + d.toString();
                            }
                            let m = dt.getUTCMonth();
                            if (m < 10) {
                                m = "0" + m.toString();
                            }
                            let y = dt.getUTCFullYear();
                            let date = parseInt(y.toString() + m + d);
                            if (tokens[i].exp_date >= date) {
                                //await firebase.validateToken(tkn);
                                validated = true;
                            } else {
                                console.log("that token is expired");
                            }
                        } else {
                            console.log("that token has already been used");
                        }
                    }
                }
            } else {
                console.log("there are no valid tokens at all");
            }
        } else {
            console.log("invalid url");
            console.log("mayb check if user has login access?");
        }
        document.getElementsByClassName("spinner")[0].style.display = "none";      
        if (validated) {
            document.getElementsByClassName("LoginFormValidated")[0].style.display = "block";
        } else {
            document.getElementsByClassName("LoginFormInvalidated")[0].style.display = "block";
        }
    }

    useEffect(() => {
        if (pageLoaded === 0) {
            begin();
            let savedEmail = sessionStorage.getItem("savedEmail");        
            if (savedEmail) {
                history.push("/home");
            }
        }
    });

    async function handleLogin(event) {
        event.preventDefault();
        console.log("loggin in user");
        console.log(email);
        let valid = await firebase.loginUser(email, password);
        if (valid) {
            sessionStorage.setItem("savedEmail", email);
            history.push("/home");
        } else {
            console.log("invalid credentials");
        }
    }

    async function handleSignup(event) {
        document.getElementsByClassName("userAlert")[0].style.display = "none";
        event.preventDefault();
        if (password !== password2) {
            console.log("passwords no match");
            togglePasswordAlert();
        } else {
            console.log("passwords match");
            let valid = await firebase.creatingUser(email, password, firstName, lastName, address, phoneNumber, token);
            if (valid) {
                sessionStorage.setItem("savedEmail", email);
                history.push("/home");                
            } else {
                toggleUserAlert();
            }
        }
    }

    function togglePasswordAlert() {
        if (document.getElementsByClassName("passwordsAlert")[0].style.display === "block") {
            document.getElementsByClassName("passwordsAlert")[0].style.display = "none";
        } else {
            document.getElementsByClassName("passwordsAlert")[0].style.display = "block";
        }
    }

    function toggleUserAlert() {
        if (document.getElementsByClassName("userAlert")[0].style.display === "block") {
            document.getElementsByClassName("userAlert")[0].style.display = "none";
        } else {
            document.getElementsByClassName("userAlert")[0].style.display = "block";
        }
    }

    // async function writeDB() {
    //     await firebase.writeToken();
    // }

    // async function resetToken() {
    //     await firebase.resetToken();
    // }

    return (
        <div className="LoginForm">
            <Spinner animation="border" className="spinner" />

            <div className="LoginFormInvalidated" style={{display:"none"}}>
                {/*
                You do not have permission to access this site.
                <button onClick={resetToken}>reset token</button>
                */}
                <Form onSubmit={handleLogin}>
                    <Form.Group controlId="formBasicEmailLogin">
                        <Form.Label>Email address</Form.Label>
                        <Form.Control required value={email} onChange={(e) => setEmail(e.target.value)} type="email" placeholder="Enter email" />
                    </Form.Group>
                    <Form.Group controlId="formBasicPasswordLogin">
                        <Form.Label>Password</Form.Label>
                        <Form.Control required value={password} onChange={(e) => setPassword(e.target.value)} name="password" type="password" placeholder="Password" />
                    </Form.Group>
                    <Form.Group controlId="formBasicCheckbox">
                        <Form.Check type="checkbox" label="Remember my login details" />
                    </Form.Group>
                    <Button variant="primary" type="submit">
                        Submit
                    </Button>
                </Form>
            </div>
            <div className="LoginFormValidated" style={{display:"none"}}>
                <Form onSubmit={handleSignup}>
                    <Form.Group controlId="formBasicFirstName">
                        <Form.Label>First Name</Form.Label>
                        <Form.Control required value={firstName} onChange={(e) => setFirstname(e.target.value)} type="text" placeholder="Enter first name" />
                    </Form.Group>
                    <Form.Group controlId="formBasicLastName">
                        <Form.Label>Last Name</Form.Label>
                        <Form.Control required value={lastName} onChange={(e) => setLastname(e.target.value)} type="text" placeholder="Enter last name" />
                    </Form.Group>
                    <Form.Group controlId="formBasicEmail">
                        <Form.Label>Email address</Form.Label>
                        <Form.Control required value={email} onChange={(e) => setEmail(e.target.value)} type="email" placeholder="Enter email" />
                    </Form.Group>
                    <div className="userAlert" style={{display:"none"}}>
                        <Alert variant="danger" onClose={() => toggleUserAlert()} dismissible>
                            <p>Error: that email is already taken</p>
                        </Alert>
                    </div>
                    <Form.Group controlId="formBasicPhone">
                        <Form.Label>Phone Number</Form.Label>
                        <Form.Control required value={phoneNumber} onChange={(e) => setPhonenumber(e.target.value)} type="text" placeholder="Enter Phone Number" />
                    </Form.Group>
                    <Form.Group controlId="formBasicAddress">
                        <Form.Label>Address</Form.Label>
                        <Form.Control required value={address} onChange={(e) => setAddress(e.target.value)} type="text" placeholder="Enter Address" />
                    </Form.Group>
                    <Form.Group controlId="formBasicPassword">
                        <Form.Label>Password</Form.Label>
                        <Form.Control required value={password} onChange={(e) => setPassword(e.target.value)} name="password" type="password" placeholder="Password" />
                    </Form.Group>
                    <Form.Group controlId="formBasicPassword2">
                        <Form.Label>Password Confirm</Form.Label>
                        <Form.Control required value={password2} onChange={(e) => setPassword2(e.target.value)} name="password2" type="password" placeholder="Password Confirm" />
                    </Form.Group>
                    <div className="passwordsAlert" style={{display:"none"}}>
                        <Alert variant="danger" onClose={() => togglePasswordAlert()} dismissible>
                            <p>Error: passwords do not match</p>
                        </Alert>
                    </div>
                    <Button variant="primary" type="submit">
                        Submit
                    </Button>
                </Form>
            </div>     

            
            {/*<button onClick={writeDB}>write db</button>*/}
            
        </div>      
    );
}

export default LoginForm;
