import React, {useEffect} from 'react';
import { useHistory } from "react-router-dom";
import Carousel from 'react-bootstrap/Carousel';
import field1 from './img/field1.jpg';
import field2 from './img/field2.jpg';
import field3 from './img/field3.jpg';
import field4 from './img/field4.jpg';
import Card from 'react-bootstrap/Card';
import Button from 'react-bootstrap/Button';
import foodDonate from './img/foodDonate.jpg';
import clothesDonate from './img/clothesDonate.jpg';
import moneyDonate from './img/moneyDonate.jpg';

const Home = () => {
    const history = useHistory();
    
    useEffect(() => {
        let savedEmail = sessionStorage.getItem("savedEmail");        
        if (savedEmail) {
            console.log("savedmail is " + savedEmail);
            styling();
        } else {
            history.push("/");
        }
    });

    function styling() {
        let chapters = document.getElementsByClassName("chapter");
        for (let i = 0; i < chapters.length; i++) {
            if (i % 2 !== 0) {
                chapters[i].style.backgroundColor = "#DADADA";
            }
        }
        document.getElementsByClassName("Navbartop")[0].style.display = "block";
        document.getElementsByClassName("Footerbottom")[0].style.display = "block";
    }

    function donateMoney() {
        history.push("/donate-money");
    }
    
    function donateFood() {
        history.push("/donate-food");
    }

    return (
        <div className="Home">
            <div className="HomeContent">
                <br />
                <div className="chapter">
                    <div className="carouselSlides">
                        <Carousel>
                            <Carousel.Item interval={3000}>
                                <img
                                className="d-block w-100"
                                src={field1}
                                alt="First slide"
                                />
                                <Carousel.Caption>
                                <h3>First slide label</h3>
                                <p>Nulla vitae elit libero, a pharetra augue mollis interdum.</p>
                                </Carousel.Caption>
                            </Carousel.Item>
                            <Carousel.Item interval={3000}>
                                <img
                                className="d-block w-100"
                                src={field2}
                                alt="Second slide"
                                />
                                <Carousel.Caption>
                                <h3>Second slide label</h3>
                                <p>Lorem ipsum dolor sit amet, consectetur adipiscing elit.</p>
                                </Carousel.Caption>
                            </Carousel.Item>
                            <Carousel.Item interval={3000}>
                                <img
                                className="d-block w-100"
                                src={field3}
                                alt="Third slide"
                                />
                                <Carousel.Caption>
                                <h3>Third slide label</h3>
                                <p>Praesent commodo cursus magna, vel scelerisque nisl consectetur.</p>
                                </Carousel.Caption>
                            </Carousel.Item>
                        </Carousel>
                    </div>                    
                </div>

                <br />
                <div className="chapter">
                    <table className="homeTable">
                        <tbody>
                            <tr>
                                <td>
                                    <p>With your donations, food and supplies can be delivered to the poor faimlies in Lebanon.</p>
                                </td>
                                <td>
                                    <img src={field4} alt="imagePic"></img>
                                </td>
                            </tr>
                        </tbody>
                    </table>
                </div>

                <br /><br />
                <div className="chapter">
                    <div id="donation">
                        <h3>Make a donation</h3>
                        <br />
                        <table className="donationTable">
                            <tbody>
                                <tr>
                                    <td>
                                        <Card>
                                            <Card.Img variant="top" src={foodDonate} />
                                            <Card.Body>
                                                <Card.Title>Food</Card.Title>
                                                <Card.Text>
                                                Donate much needed food to families.
                                                </Card.Text>
                                                <Button variant="primary"  onClick={donateFood}>Donate food</Button>
                                            </Card.Body>
                                        </Card>
                                    </td>
                                    <td>
                                        <Card>
                                            <Card.Img variant="top" src={clothesDonate} />
                                            <Card.Body>
                                                <Card.Title>Clothes and other items</Card.Title>
                                                <Card.Text>
                                                Donate clothes to old and yound as well as other items.
                                                </Card.Text>
                                                <Button variant="primary">Donate items</Button>
                                            </Card.Body>
                                        </Card>
                                    </td>
                                    <td>
                                        <Card>
                                            <Card.Img variant="top" src={moneyDonate} />
                                            <Card.Body>
                                                <Card.Title>Make a donation</Card.Title>
                                                <Card.Text>
                                                Make a cash or bank donation to a family.
                                                </Card.Text>
                                                <Button variant="primary" onClick={donateMoney}>Donate Money</Button>
                                            </Card.Body>
                                        </Card>
                                    </td>
                                </tr>
                            </tbody>
                        </table>
                    </div>
                </div>
            </div>
            <br />
        </div>      
    );
}

export default Home;
