import React, {useEffect} from 'react';

const Footerbottom = () => {
    useEffect(() => {
        let savedEmail = sessionStorage.getItem("savedEmail");
        if (savedEmail) {
            console.log("savedmail is " + savedEmail);
            document.getElementsByClassName("Footerbottom")[0].style.display = "block";
        } else {
            document.getElementsByClassName("Footerbottom")[0].style.display = "none";
        }
    });

    return (
        <div className="Footerbottom">
            <br />
            <br />
            <footer className="bg-light text-center text-lg-start">
                <div className="text-center p-3" style={{backgroundColor:"rgba(0, 0, 0, 0.2)"}}>
                    <a className="text-dark" href="https://mdbootstrap.com/">Donating money and food to the poor</a>
                </div>
            </footer>
        </div>      
    );
}

export default Footerbottom;
