import React, {useState, useEffect} from 'react';
import { useHistory } from "react-router-dom";
import firebase from './firebase';
import Form from 'react-bootstrap/Form';
import Button from 'react-bootstrap/Button';

const MyCart = () => {
    const [pageLoaded, setPageloaded] = useState(0);
    const [name, setName] = useState("");
    const [address, setAddress] = useState("");

    const history = useHistory();
    
    useEffect(() => {
        if (pageLoaded === 0) {
            let savedEmail = sessionStorage.getItem("savedEmail");        
            let myCart = sessionStorage.getItem("myCart");
            if (savedEmail) {
                console.log("savedmail is " + savedEmail);
                getItems(myCart);
            } else {
                history.push("/");
            }
        }        
    });

    async function getItems(myCart) {
        console.log("over here");
        setPageloaded(1);
        let myList = [];
        let html = "";
        if (myCart) {    
            let myItems = myCart.split(",");            
            if (!(myItems.length === 1 && myItems[0] === "null")) {
                html = `<table class="table"><tbody><tr><th scope="col">Item</th><th scope="col">Qty</th><th scope="col">Total</th><th></th></tr>`;
                console.log(myItems);
                let food = await firebase.getFood();
                let total = 0;
                console.log(food);
                for (let i = 0; i < myItems.length; i++) {
                    for (let j = 0; j < food.length; j++) {
                        if (myItems[i] === food[j].desc) {
                            let newItem = true;
                            for (let k = 0; k < myList.length; k++) {
                                if (myList[k].desc === myItems[i]) {
                                    let item = {
                                        desc: myList[k].desc,
                                        qty: myList[k].qty + 1,
                                        total: parseFloat(myList[k].total) + parseFloat(food[j].price)
                                    }
                                    myList[k] = item;
                                    k = myList.length;
                                    newItem = false;
                                }
                            }
                            if (newItem === true) {
                                let item = {
                                    desc: myItems[i],
                                    qty: 1,
                                    total: parseFloat(food[j].price)
                                }
                                myList.push(item);
                            }
                            j = food.length;
                        }
                    }
                }

                for (let i = 0; i < myList.length; i++) {
                    html += `<tr><td>` + myList[i].desc + `</td><td><button type="button" class="addToOrder btn btn-light" value="` + myList[i].desc + `">+</button>` + myList[i].qty + `<button type="button" class="removeFromOrder btn btn-light" value="` + myList[i].desc + `">-</button></td><td>$` + myList[i].total + `</td><td><button class='removeItemBtn btn btn-danger btn-sm' value="` + myList[i].desc + `">Remove item</button></td></tr>`;
                    total += parseFloat(myList[i].total);
                }
                html += `<tr><td><b>Total</b></td><td></td><td><b>$` + total + `</b></td><td></td></tr>`;
                html += `</tbody></table>`;
            } else {
                html = "<p>You haven't selected any items to be donated. Please <a href='#donate-food'>Click here</a> to select items to donate</p>";
            }        
        } else {
            html = "<p>You haven't selected any items to be donated. Please <a href='#donate-food'>Click here</a> to select items to donate</p>";
        } 
        document.getElementById("myItems").innerHTML = html;
        console.log("myCart:" + myCart);
        await setBtns();
    }

    async function setBtns() {
        let btns = document.getElementsByClassName("removeItemBtn");
        for (let i = 0; i < btns.length; i++) {
            btns[i].onclick = async function(){
                removeCartItem(btns[i].value);
            }
        }

        let btnsAdd = document.getElementsByClassName("addToOrder");
        for (let i = 0; i < btnsAdd.length; i++) {
            btnsAdd[i].onclick = async function(){
                addToCart(btnsAdd[i].value);
            }
        }

        let btnsRem = document.getElementsByClassName("removeFromOrder");
        for (let i = 0; i < btnsRem.length; i++) {
            btnsRem[i].onclick = async function(){
                removeFromCart(btnsRem[i].value);
            }
        }
    }

    function removeFromCart(item) {
        let myCart = sessionStorage.getItem("myCart");
        myCart = myCart.split(",");
        for (let j = 0; j < myCart.length; j++) {
            if (myCart[j] === item) {
                myCart.splice(j, 1);
                j = myCart.length;
            }
        }
        myCart = myCart.toString();
        sessionStorage.setItem("myCart", myCart);
        getItems(myCart);
    }

    function addToCart(item) {
        let myCart = sessionStorage.getItem("myCart");
        myCart += "," + item;
        sessionStorage.setItem("myCart", myCart);
        getItems(myCart);
    }

    function removeCartItem(item) {
        console.log("item was " + item);
        let myCart = sessionStorage.getItem("myCart");
        myCart = myCart.split(",");
        for (let i = 0; i < myCart.length; i++) {
            if (myCart[i] === item) {
                myCart.splice(i, 1);
                i = 0;
            }
        }
        myCart = myCart.toString();
        sessionStorage.setItem("myCart", myCart);
        getItems(myCart);
    }

    async function confirmation() {
        let savedEmail = sessionStorage.getItem("savedEmail");
        let myCart = sessionStorage.getItem("myCart");
        let orderDetails = sessionStorage.getItem("orderDetails");
        let obj = JSON.parse(orderDetails);
        let myCart2 = myCart.split(",");
        let food = await firebase.getFood();
        let total = 0;
        for (let i = 0; i < myCart2.length; i++) {
            for (let j = 0; j < food.length; j++) {
                if (myCart2[i] === food[j].desc) {
                    total += food[j].price;
                    j = food.length;
                }
            }
        }
        let pass = 1;        
        if (total <= 0) {
            pass = 0;
            addNotification("error", "Your order is invalid");
        }
        if (obj) {
            if (obj.name === "" || obj.address === "") {
                pass = 0;
                addNotification("alert-danger", "Your recipient cannot be left blank");
            }
        } else {
            pass = 0;
            addNotification("alert-danger", "Your recipient cannot be left blank");
        }
        
        if (pass === 1) {
            let receipt = await firebase.createOrder(savedEmail, myCart, total, orderDetails);
            history.push("/confirmation?receipt=" + receipt + "#");
        }
    }

    function addNotification(type, msg) {
        let html = document.getElementById("alertNotification").innerHTML;
        let notificationCount = document.getElementsByClassName("notificationBadge").length;
        html += `
        <div class="notificationBadge">
            <div class="` + type + `" role="alert">
                ` + msg + `
            </div>
        </div>                
        `;
        document.getElementById("alertNotification").innerHTML = html;
        // document.getElementById("notificationBadge" + notificationCount).style.opacity = 1;
        // document.getElementById("notificationBadge" + notificationCount).style.transition = "all 1s";                
        // document.getElementById("notificationBadge" + notificationCount).style.opacity = 0;
        setTimeout(function(){ 
            if(document.getElementsByClassName("notificationBadge")[notificationCount]) {
                document.getElementsByClassName("notificationBadge")[notificationCount].style.display = "none"; 
            }
        }, 4000);
    }

    function setDetails(event) {
        event.preventDefault();
        let orderDetails = {
            name: name,
            address: address
        };
        sessionStorage.setItem("orderDetails", JSON.stringify(orderDetails));
        confirmation();
    }

    function donateMore() {
        history.push("/donate-food");
    }

    return (
        <div className="MyCart">
            <div className="content">
                <br />
                <div id="alertNotification"></div>     
                <br />
                <h3>My Cart</h3>
                <br />
                <div id="myItems"></div>
                <br />
                <br />
                <button type="button" className="btn btn-success" onClick={donateMore}>Make more donations</button>
                <br />       

                <hr></hr>
                <h4>Delivery details</h4>
                <p>Please enter the name and address you wish these items to be donated to</p>
                <div className="recipient">
                    <Form onSubmit={setDetails} autoComplete="off">
                        <Form.Group controlId="formName">
                            <Form.Label>Name of recipient (the person or people receiving the item)</Form.Label>
                            <Form.Control required value={name} onChange={(e) => setName(e.target.value)} type="text" placeholder="Name" />
                        </Form.Group>
                        <Form.Group controlId="formAddress">
                            <Form.Label>Address</Form.Label>
                            <Form.Control required value={address} onChange={(e) => setAddress(e.target.value)} type="text" placeholder="Address" />
                        </Form.Group>
                        <Button variant="primary" type="submit">
                            Proceed with payment options
                        </Button>
                    </Form>
                </div>
                <br />
                {/*}
                <hr></hr>
                <br />
                <button type="button" className="btn btn-success" onClick={confirmation}>Proceed with payment options</button>
                */}

                
            </div> 
        </div>      
    );
}

export default MyCart;
