    async creatingUser(email, password, firstName, lastName, address, phone, token) {
        console.log("adding user to database...");
        // check if user is valid first
        let x = await firebase.database().ref("care_users").once('value');
        let emailEncrypted = CryptoJS.AES.encrypt(email, "emailEncrypted").toString();
        let valid = true;
        for (let k in x.val()) {
            if (x.val()[k].email) {
                if (email === CryptoJS.AES.decrypt(x.val()[k].email, "emailEncrypted").toString(CryptoJS.enc.Utf8)) {
                    console.log("error: that user already exists");
                    valid = false;
                }
            }
        }
        if (valid) {
            let passwordEncrypted = CryptoJS.AES.encrypt(password, "passwordEncrypted").toString();
            let firstNameEncrypted = CryptoJS.AES.encrypt(firstName, "firstNameEncrypted").toString();
            let lastNameEncrypted = CryptoJS.AES.encrypt(lastName, "lastNameEncrypted").toString();
            let addressEncrypted = CryptoJS.AES.encrypt(address, "addressEncrypted").toString();
            let phoneEncrypted = CryptoJS.AES.encrypt(phone, "phoneEncrypted").toString();
            console.log(emailEncrypted);
            console.log(CryptoJS.AES.decrypt(emailEncrypted, "emailEncrypted").toString());
            console.log(CryptoJS.AES.decrypt(emailEncrypted, "emailEncrypted").toString(CryptoJS.enc.Utf8));
    
            await firebase.database().ref("care_users").push({
                email: emailEncrypted,
                password: passwordEncrypted,
                firstName: firstNameEncrypted,
                lastName: lastNameEncrypted,
                address: addressEncrypted,
                phone : phoneEncrypted
            });
            console.log("user added");
            await firebase.validateToken(token);
        }
        return valid;        
    }

    async loginUser(email, password) {
        console.log("adding user to database...");
        // check if user is valid first
        let x = await firebase.database().ref("care_users").once('value');
        let valid = false;
        for (let k in x.val()) {
            if (x.val()[k].email) {
                if (email === CryptoJS.AES.decrypt(x.val()[k].email, "emailEncrypted").toString(CryptoJS.enc.Utf8)) {
                    if (password === CryptoJS.AES.decrypt(x.val()[k].password, "passwordEncrypted").toString(CryptoJS.enc.Utf8)) {
                        valid = true;
                    }
                }
            }
        }
        return valid;
    }

