import React, {useState, useEffect} from 'react';
import { useHistory } from "react-router-dom";
import Form from 'react-bootstrap/Form';
import Button from 'react-bootstrap/Button';
import Accordion from 'react-bootstrap/Accordion';
import Card from 'react-bootstrap/Card';


const DonateMoney = () => {
    const [cardNumber, setCardnumber] = useState("");
    const [name, setName] = useState("");

    const history = useHistory();
    
    useEffect(() => {
        let savedEmail = sessionStorage.getItem("savedEmail");        
        if (savedEmail) {
            console.log("savedmail is " + savedEmail);
            setReference(savedEmail);
        } else {
            history.push("/");
        }
    });

    function setReference(savedEmail) {
        if (document.getElementById("referenceID")) {
            if (savedEmail) {
                document.getElementById("referenceID").innerHTML = savedEmail;
            } else {
                document.getElementById("referenceID").innerHTML = "<i>Your email address</i>";
            }
        } 
    }

    function makeCardDonation() {
        console.log("makeing card donaiton");
    }

    return (
        <div className="DonateMoney">
            <div className="content">
                <br />
                <h3>Donate Money</h3>
                <br />
                <p>Thank you for your generosity. Please fill in the form to make a donation:</p>
                <br />
                <div className="paymentSection">
                    <Accordion defaultActiveKey="0">
                        <Card>
                            <Card.Header>
                            <Accordion.Toggle as={Button} variant="link" eventKey="0">
                                Bank transfer
                            </Accordion.Toggle>
                            </Card.Header>
                            <Accordion.Collapse eventKey="0">
                            <Card.Body>
                                <table>
                                    <tbody>
                                        <tr>
                                            <td>BSB:</td>
                                            <td>0123 4567</td>
                                        </tr>
                                        <tr>
                                            <td>Account Number:</td>
                                            <td>0123 4567 8901</td>
                                        </tr>
                                        <tr>
                                            <td>Reference:</td>
                                            <td id="referenceID">0123 4567 8901</td>
                                        </tr>
                                    </tbody>
                                </table>
                            </Card.Body>
                            </Accordion.Collapse>
                        </Card>
                        <Card>
                            <Card.Header>
                            <Accordion.Toggle as={Button} variant="link" eventKey="1">
                                Credit Card Payment
                            </Accordion.Toggle>
                            </Card.Header>
                            <Accordion.Collapse eventKey="1">
                            <Card.Body>
                                <Form onSubmit={makeCardDonation}>
                                    <Form.Group controlId="formBasicCardNumber">
                                        <Form.Label>Card Number</Form.Label>
                                        <Form.Control required value={cardNumber} onChange={(e) => setCardnumber(e.target.value)} type="text" placeholder="Enter Card Number" />
                                    </Form.Group>
                                    <Form.Group controlId="formBasicName">
                                        <Form.Label>Name on card</Form.Label>
                                        <Form.Control required value={name} onChange={(e) => setName(e.target.value)} type="text" placeholder="Name on card" />
                                    </Form.Group>
                                    <Button variant="primary" type="submit">
                                        Submit
                                    </Button>
                                </Form>
                            </Card.Body>
                            </Accordion.Collapse>
                        </Card>
                    </Accordion>
                </div>                
            </div>
            <br />
        </div>      
    );
}

export default DonateMoney;
