import React, {useState, useEffect} from 'react';
import { useHistory } from "react-router-dom";
import firebase from './firebase';
import Form from 'react-bootstrap/Form';
import Spinner from 'react-bootstrap/Spinner';
import CryptoJS from 'crypto-js';
import Button from 'react-bootstrap/Button';

const Admin = () => {
    const [pageLoaded, setPageloaded] = useState(0);
    const [desc, setDesc] = useState("");
    const [src, setSrc] = useState("");
    const [price, setPrice] = useState("");
    const [receipt, setReceipt] = useState("");
    const history = useHistory();
    
    useEffect(() => {
        if (pageLoaded === 0) {
            let savedEmail = sessionStorage.getItem("savedEmail");
            if (savedEmail) {
                console.log("savedmail is " + savedEmail);
                document.getElementsByClassName("content")[0].style.display = "none";
                if(adminCheck()) {
                    document.getElementsByClassName("content")[0].style.display = "block";
                    getFood();
                    getPendingOrders();
                    getCompletedOrders();
                } else {
                    history.push("/");
                }
            } else {
                history.push("/");
            }
        }        
    });

    function adminCheck() {
        let pass = prompt("Admin Password");
        console.log("pass is ");
        let passEncrypt = CryptoJS.AES.encrypt(pass, "passEncrypted").toString();
        console.log(CryptoJS.AES.decrypt(passEncrypt, "passEncrypted").toString());
        if (CryptoJS.AES.decrypt(passEncrypt, "passEncrypted").toString() === "61646d696e") {
            return true;
        }
        return false;

    }

    async function getPendingOrders() {
        let pendingOrders = await firebase.getPendingOrders();
        let html = "";
        for (let p = 0; p < pendingOrders.length; p++) {
            let items = pendingOrders[p].items.split(",");
            let myItems = [];
            let myItemsHtml = "";
            for (let i = 0; i < items.length; i++) {
                if (!myItems.includes(items[i])) {
                    if (items[i] !== "undefined" && items[i] !== "null") {
                        myItems.push(items[i]);
                        let qty = 1;
                        for (let j = i+1; j < items.length; j++) {
                            if (items[i] === items[j]) {
                                qty += 1;
                            }
                        }
                        myItemsHtml += `
                            <p>` + items[i] + ` x ` + qty + `
                        `;
                    }
                }
            }
            let deliveryDetails = JSON.parse(pendingOrders[p].details);
            let name = await firebase.getUserDetails(pendingOrders[p].user);
            html += `
                <div class="order">
                    <p><b>User: </b>` + name + `</p>
                    <p><b>Total: $</b>` + pendingOrders[p].total + `</p>
                    <p><b>Items: </b></p>
                    ` + myItemsHtml + `
                    <p><b>Delivery Recepient: </b>` + deliveryDetails.name + `</p>
                    <p><b>Delivery Recepient Address: </b>` + deliveryDetails.address + `</p>
                    <p><b>Receipt: </b>` + pendingOrders[p].receipt + `</p>
                    <button class='completed btn btn-primary' value='` + pendingOrders[p].receipt + `'>Set order as completed</button>
                </div>
            `;
        }

        document.getElementsByClassName("pendingOrders")[0].style.display = "block";
        document.getElementsByClassName("pendingOrders")[0].innerHTML = html;
        await setCompleted();
        console.log("done with pending orders");

    }

    async function setCompleted() {
        let btns = document.getElementsByClassName("completed");
        for (let i = 0; i < btns.length; i++) {
            btns[i].onclick = async function(){
                await firebase.validateOrder(btns[i].value)
            }
        }
    }
    
    async function getCompletedOrders() {
        let confirmedOrders = await firebase.getConfirmedOrders();
        let html = "";
        for (let p = 0; p < confirmedOrders.length; p++) {
            let items = confirmedOrders[p].items.split(",");
            let myItems = [];
            let myItemsHtml = "";
            for (let i = 0; i < items.length; i++) {
                if (!myItems.includes(items[i])) {
                    if (items[i] !== "undefined" && items[i] !== "null") {
                        myItems.push(items[i]);
                        let qty = 1;
                        for (let j = i+1; j < items.length; j++) {
                            if (items[i] === items[j]) {
                                qty += 1;
                            }
                        }
                        myItemsHtml += `
                            <p>` + items[i] + ` x ` + qty + `
                        `;
                    }
                }
            }
            let deliveryDetails = JSON.parse(confirmedOrders[p].details);
            let name = await firebase.getUserDetails(confirmedOrders[p].user);
            html += `
                <div class="order">
                    <p><b>User: </b>` + name + `</p>
                    <p><b>Total: $</b>` + confirmedOrders[p].total + `</p>
                    <p><b>Items: </b></p>
                    ` + myItemsHtml + `
                    <p><b>Delivery Recepient: </b>` + deliveryDetails.name + `</p>
                    <p><b>Delivery Recepient Address: </b>` + deliveryDetails.address + `</p>
                    <p><b>Receipt: </b>` + confirmedOrders[p].receipt + `</p>
                </div>
            `;
        }

        document.getElementsByClassName("completedOrders")[0].style.display = "block";
        document.getElementsByClassName("completedOrders")[0].innerHTML = html;
        console.log("done with confirmed orders");

    }

    async function getFood() {
        // sessionStorage.setItem("myCart", null);
        document.getElementsByClassName("spinner")[0].style.display = "block";
        setPageloaded(1);
        let food = await firebase.getFood();        
        let html = "";
        if (food.length > 0) {
            html += "<table class='productList'><tbody>"
            for (let i = 0; i < food.length; i++) {
                if (i % 4 === 0) {
                    if (i === 0) {
                        html += "<tr>"
                    } else {
                        html += "</tr><tr>"
                    }
                }
                html += `
                <td>
                    <div class='card'>                    
                        <h5 class='desc'>` + food[i].desc + ` - $` + food[i].price + `</h5>
                        <img card-img-top src="` + food[i].src + `"></img>
                        <br>
                        <button class='removeItem btn btn-danger btn-sm' value="` + food[i].desc + `">Remove item</button>
                    </div>
                </td>
                `;    
                
            }
            html += "</tr></tbody></table>";
            document.getElementById("foodItems").innerHTML = html;
            document.getElementsByClassName("spinner")[0].style.display = "none";
            await removeItem();            
        }
    }
    
    async function removeItem() {
        let btns = document.getElementsByClassName("removeItem");
        for (let i = 0; i < btns.length; i++) {
            btns[i].onclick = async function(){
                //console.log("item si " + btns[i].value);
                await firebase.removeItem(btns[i].value);
                // show notification
                addNotification(`alert alert-warning`, `Removed item <strong>` + btns[i].value + `</strong> from database. Please refresh the page to see effect.`);
            }
        }
    }

    async function addItem(event) {
        event.preventDefault();
        let food = await firebase.getFood();        
        let proceed = true;
        for (let i = 0; i < food.length; i++) {
            if (food[i].desc === desc) {
                //console.log("that item is already in database");
                addNotification("alert alert-warning", `"` + desc + `" is already in the database. Please remove it if you want to proceed`);
                proceed = false;
            } 
        }
        if (proceed === true) {
            let p = price.replace("$", "");
            await firebase.addItem(desc, src, p);
            addNotification("alert alert-success", `"` + desc + `" has been added to the database`);
            setDesc("");
            setPrice("");
            setSrc("");
        }
    }

    function addNotification(type, msg) {
        let html = document.getElementById("alertNotification").innerHTML;
        let notificationCount = document.getElementsByClassName("notificationBadge").length;
        html += `
        <div class="notificationBadge">
            <div class="` + type + `" role="alert">
                ` + msg + `
            </div>
        </div>                
        `;
        document.getElementById("alertNotification").innerHTML = html;
        // document.getElementById("notificationBadge" + notificationCount).style.opacity = 1;
        // document.getElementById("notificationBadge" + notificationCount).style.transition = "all 1s";                
        // document.getElementById("notificationBadge" + notificationCount).style.opacity = 0;
        setTimeout(function(){ 
            if(document.getElementsByClassName("notificationBadge")[notificationCount]) {
                document.getElementsByClassName("notificationBadge")[notificationCount].style.display = "none"; 
            }
        }, 4000);
    }

    async function filterOrder(event) {
        event.preventDefault();
        //7BE93N3
        let details = await firebase.getOrderById(receipt);
        if (details !== 0) {
            let items = details.items.split(",");
            console.log(details);
            let myItems = [];
            let myItemsHtml = "";
            for (let i = 0; i < items.length; i++) {
                if (!myItems.includes(items[i])) {
                    if (items[i] !== "undefined" && items[i] !== "null") {
                        myItems.push(items[i]);
                        let qty = 1;
                        for (let j = i+1; j < items.length; j++) {
                            if (items[i] === items[j]) {
                                qty += 1;
                            }
                        }
                        myItemsHtml += `
                            <p>` + items[i] + ` x ` + qty + `
                        `;
                    }
                }
            }
            let deliveryDetails = JSON.parse(details.details);
            let name = await firebase.getUserDetails(details.user);
            document.getElementById("filteredOrder").style.display = "block";
            document.getElementById("filteredOrder").innerHTML = `
                <p><b>User: </b>` + name + `</p>
                <p><b>Total: </b>` + details.total + `</p>
                <p><b>Items: </b></p>
                ` + myItemsHtml + `
                <p><b>Delivery Recepient: </b>` + deliveryDetails.name + `</p>
                <p><b>Delivery Recepient Address: </b>` + deliveryDetails.address + `</p>
                <p><b>Status: </b>` + details.confirmed + `</p>
            `;

        } else {
            document.getElementById("filteredOrder").innerHTML = receipt + " is an invalid receipt";

        }
    }

    return (
        <div className="Admin">
            <div className="content" style={{display:"none"}}>
                <br />
                <div id="alertNotification"></div>                
                <br />
                <h3>Admin page</h3>

                {/* form to add food to database */}
                <div style={{display:"block"}}>
                    <hr></hr>
                    <h5>Add food items to database</h5>
                    <Form onSubmit={addItem} autoComplete="off">
                        <Form.Group controlId="formDesc">
                            <Form.Label>Description</Form.Label>
                            <Form.Control required value={desc} onChange={(e) => setDesc(e.target.value)} type="text" placeholder="Description" />
                        </Form.Group>
                        <Form.Group controlId="formSrc">
                            <Form.Label>Image link</Form.Label>
                            <Form.Control required value={src} onChange={(e) => setSrc(e.target.value)} type="text" placeholder="Image link" />
                        </Form.Group>
                        <Form.Group controlId="formPrc">
                            <Form.Label>Price</Form.Label>
                            <Form.Control required value={price} onChange={(e) => setPrice(e.target.value)} type="text" placeholder="Price" />
                        </Form.Group>
                        <Button variant="primary" type="submit">
                            Submit
                        </Button>
                    </Form>

                </div>
                <br />
                <br />
                
                <br />            
                <br />            
                <hr></hr>
                <h4>Food</h4>
                <Spinner animation="border" className="spinner" />
                <div id="foodItems"></div>
                <br /> 
                <hr></hr>
                <br /> 
                <h3>Orders</h3>
                <h5>Filter Order</h5>
                <div className="orderFilter">
                    <Form className="form-inline" onSubmit={filterOrder} autoComplete="off">
                        <Form.Group controlId="formInput">
                            <Form.Control required value={receipt} onChange={(e) => setReceipt(e.target.value)} type="text" placeholder="Receipt Number" />
                        </Form.Group>
                        <Button variant="primary" type="submit">
                            Submit
                        </Button>
                    </Form>
                </div>
                <br />
                <br />
                <div id="filteredOrder" style={{display:"none"}}></div>
                <br />
                <h5>Pending Orders</h5>
                <div className="pendingOrders" style={{display:"none"}}></div>
                <br />
                <h5>Completed Orders</h5>
                <div className="completedOrders" style={{display:"none"}}></div>
            </div>            
        </div>      
    );
}

export default Admin;
